body {
  color: #fff;
  background: linear-gradient(135deg, #1f003a, #0a0218);
  height: 100vh;
  margin: 0;
  padding: 0;
  font-family: Poppins, sans-serif;
  overflow-x: hidden;
}

aside {
  color: #bfbfff;
  background: linear-gradient(#090024, #130045);
  padding: 2rem;
  box-shadow: 0 8px 16px #000c;
}

aside button {
  color: #bfbfff;
  cursor: pointer;
  background: none;
  border: none;
  justify-content: start;
  align-items: center;
  gap: 1rem;
  padding: 12px 16px;
  font-weight: 600;
  transition: all .3s;
  display: flex;
}

aside button:hover {
  color: #fff;
  background: #3a007a;
  border-radius: 8px;
}

.glass {
  backdrop-filter: blur(12px);
  background: #00003280;
  border-radius: 16px;
  padding: 1.5rem;
  transition: transform .2s ease-in-out, box-shadow .2s;
  box-shadow: 0 8px 20px #0009;
}

.glass:hover {
  transform: translateY(-2px);
  box-shadow: 0 12px 24px #000c;
}

.transaction-table {
  border-collapse: collapse;
  background: #00003266;
  border-radius: 12px;
  width: 100%;
  overflow: hidden;
  box-shadow: 0 4px 12px #0009;
}

.transaction-table th {
  text-align: left;
  color: #bfbfff;
  background: #ffffff1a;
  padding: 12px;
  font-size: 1rem;
}

.transaction-table td {
  color: #ddf;
  border-bottom: 1px solid #ffffff1a;
  padding: 12px;
  font-size: .9rem;
}

.transaction-table tr:hover {
  background: #ffffff0d;
}

input {
  color: #fff;
  background: #00003233;
  border: 1px solid #0000;
  border-radius: 8px;
  outline: none;
  padding: 12px 16px;
  transition: border-color .3s, box-shadow .3s;
}

input:focus {
  border-color: #6a21ab;
  box-shadow: 0 0 8px #6a21abcc;
}

